"use strict";
/**
 * @file 
 */
Object.defineProperty(exports, "__esModule", { value: true });
class AWheel {
    constructor() {
        ////////////////////
        //
        this.list = [];
        //hashmap,valueindex
        this.valueHashMap = {};
        ////////////////////
        this.onSelectItemCallbackList = [];
    }
    /**
    * 
    */
    getOptions() {
        return this.list;
    }
    /**
    * ,
    */
    selectOption(value, showAnimation = false) {
        //valueHashMapvalue,,
        if (this.valueHashMap[value] != null) {
            var index = this.valueHashMap[value];
            this.selectIndex(index, showAnimation);
        }
    }
    /**
    * 
    */
    getValue() {
        return this.selectedValue;
    }
    /**
     * SelectItem
     * @param {{(index:number, value:any):void}} fn
     */
    addSelectItemListener(fn) {
        this.onSelectItemCallbackList.push(fn);
    }
    /**
     * SelectItem
     * @param {{(index:number, value:any):void}} fn
     */
    removeSelectItemListener(fn) {
        this.onSelectItemCallbackList = this.onSelectItemCallbackList.filter(_fn => _fn !== fn);
    }
    ////////////////////////////DOM
    getDOM() {
        return this.dom;
    }
    /////////////////////////////wheel
    /**
    * 
    * @param index			
    * @param value			
    */
    toggleSelected(index, value) {
        this.onSelectItemCallbackList.forEach(fn => {
            fn.call(this, index, value);
        });
    }
    /**
     * 
     */
    destroy() {
        this.onSelectItemCallbackList = null;
    }
    /////////////////////////////
    initMouseWheel() {
        this.getDOM().on('wheel', (e) => {
            // 
            let sign = Math.sign(e.deltaY);
            if (sign > 0 && this.selectedIndex < this.list.length - 1) {
                this.selectIndex(this.selectedIndex + 1);
            }
            else if (sign < 0 && this.selectedIndex > 0) {
                this.selectIndex(this.selectedIndex - 1);
            }
        });
    }
}
exports.AWheel = AWheel;



